.book_card {
  background: #ffffff;
  // box-shadow   : 0px 4px 4px rgba(0, 0, 0, 0.25), inset 0px 4px 4px rgba(0, 0, 0, 0.25), inset 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 10px;
  // padding      : 15px 15px 0;

  .card {
    transition: all 0.3s ease-in-out;
    &:hover {
      transition: all 0.3s ease-in-out;
      box-shadow: 0 0 10px rgba(0, 109, 124, 0.7);
      transform: scale(1.01);
    }

    .card-img-top {
      width: 100%;
      height: 260px;
      object-fit: cover;
    }

    .card_text {
      font-style: normal;
      font-weight: 600;
      font-size: 16px;
      line-height: 16px;
      text-align: center;
      color: #006d7c;

      &:last-child {
        margin-top: 3px;
      }
    }
  }
}
