.filter_card {
  background-color: #FFFFFF;
  // box-shadow      : 0px 4px 4px rgba(0, 0, 0, 0.25), inset 0px 4px 4px rgba(0, 0, 0, 0.25), inset 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius   : 5px;
  border: 1px solid rgba(0, 109, 124, 0.5);
  box-shadow: 0 0 10px rgba(0, 109, 124, 0.2);
  padding         : 20px;

  .filter_form {
    max-height: 260px;
    overflow-y: auto;
    .css-ahj2mt-MuiTypography-root{
      color: #006d7c;
    }
    .form-check {
      margin-top: 10px;
      color: #006d7c;
    }
  }

  .filter_form.languages {
    max-height: 131px;
    overflow-y: auto;
  }
}

.filter_card.languages {
  margin-top: 18px;
}